var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"contenttext"},[_c('pre',[_vm._v("        传统上位机软件如WINCC、组态王、INTOUCH，其功能细化至界面的拖拽、通讯点位的下拉配置等，对于缺乏编程基础的用户来说，极为方便快捷。但其缺点也很明显：通用功能不适应特殊情况、脚本系统不够强大、难以扩展自定义功能、三方工具使用不方便等。\n        然而近年来机器人、AGV、无人工厂成为工业的主流，物联网、大数据渐成亮点，"),_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("编程基础几乎成为技术人员的标配")]),_vm._v("，实际生产应用中常常遇到标准版软件无法支持的特殊场景，例如对接MES、对BCR进行复杂通讯控制、向贴标机发数据、与机器人进行复杂通讯等，受限于通用软件功能的通用性，特殊情况甚至没有解决方案，技术人员无技可施，而软件厂商对新功能的支持又需经历评估、研发、测试、版本升级等过程..\n        "),_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("\n        那么是否有一种专为编程人员设计的程序框架，满足必备功能的同时，支持更为灵活的定制开发？ \n        ")]),_vm._v("\n        StateManager就是这样一个微框架：\n        1.内核主要完成周期性逻辑指令的执行，其他功能均为插件式挂接。\n        2.很方便的对接物联网、对接MES、云服务、云数据等各种新场景的应用，能够自行开发各种特殊功能插件，能够直接调用各种现有三方通讯组件。\n        3.借鉴了WINCC的信号触发机制，但相比WINCC兼容的通讯类型更多，通讯方式更直接、脚本执行效率更高。\n        4.信号采集是通过脚本或代码调用通讯接口按需、同步、直接从设备采集，降低了数据流量的同时提高了数据实时性，当然也可灵活运用为周期采集。\n        5.实现任意复杂的逻辑，将逻辑表达为状态图，按状态图编写脚本即可。逻辑可以编写为C#类库、JS/VBS脚本、DLL插件。\n        6.不需要购买高额授权，没有对接设备苦恼，我们已经开发好很多常用的功能插件。\n        7.我们如何盈利：见后面的增值服务。\n        \n        关于作者：\n        作者毕业于中国石油大学电气自动化系，从事非标自动化开发工作十余年，擅长处理复杂工业控制逻辑。作者结合多年非标开发经验，耗时数年，开发和维护StateManager状态机框架，框架经过多次重构以及无数次生产环境的锤炼渐于成熟，于2019年免费开放给大家使用。\n        "),_c('span',{staticStyle:{"text-align":"left"}},[_vm._v("官方QQ群：244671697\n          "),_c('a',{attrs:{"target":"_blank","href":"//shang.qq.com/wpa/qunwpa?idkey=f5783cb90a7eaeccfe3056f3dccced4fba8c9e51cd635bd7fdf409d7e84f36b8"}},[_c('img',{attrs:{"border":"0","src":"//pub.idqqimg.com/wpa/images/group.png","alt":"StateManager官方群1","title":"StateManager官方群1"}})]),_vm._v("\n        ")]),_vm._v("\n      ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('dl',[_c('dt',[_vm._v("业务需求")]),_vm._v(" "),_c('dd',[_vm._v("跟据用户业务需求,推荐最佳软硬件规划")]),_vm._v(" "),_c('dt',[_vm._v("控制方案")]),_vm._v(" "),_c('dd',[_vm._v("包括电机、变频器、PLC、单片机、PC机、触屏、网络等系统构架，制订